___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
    background-color: #1e1e1e;
    color: #ffffff;
    user-select: none;
    -webkit-user-select: none;
    overflow: hidden;
}

#root {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

.timer-display {
    font-size: 48px;
    font-weight: bold;
    margin: 20px 0;
}

.phase-indicator {
    font-size: 18px;
    margin-bottom: 15px;
    color: #888;
}

.controls {
    display: flex;
    gap: 10px;
    margin-top: 20px;
}

button {
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    background-color: #007acc;
    color: white;
    cursor: pointer;
    transition: background-color 0.2s;
}

button:hover {
    background-color: #005999;
}

.progress-bar {
    width: 100%;
    height: 4px;
    background-color: #333;
    border-radius: 2px;
    overflow: hidden;
    margin: 10px 0;
}

.progress-bar-fill {
    height: 100%;
    background-color: #007acc;
    transition: width 1s linear;
}

.title-bar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 32px;
    background-color: #252525;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    -webkit-app-region: drag;
}

.window-controls {
    -webkit-app-region: no-drag;
}

.window-control {
    background: none;
    border: none;
    color: #888;
    padding: 8px;
    margin-left: 5px;
}

.window-control:hover {
    background-color: #333;
}

/* App 组件样式 */
.app {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.content {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
}

.navigation {
    display: flex;
    justify-content: space-around;
    padding: 10px;
    background-color: #252525;
    border-top: 1px solid #333;
}

.navigation button {
    background: none;
    border: none;
    color: #888;
    padding: 8px 16px;
    cursor: pointer;
    transition: color 0.2s;
}

.navigation button:hover,
.navigation button.active {
    color: #fff;
}

/* 设置组件样式 */
.settings {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
}

.settings h2 {
    color: #fff;
    margin: 20px 0 10px;
    font-size: 18px;
}

.setting-item {
    margin: 10px 0;
    display: flex;
    align-items: center;
}

.setting-item label {
    flex: 1;
    color: #ccc;
}

.setting-item input[type="number"] {
    width: 60px;
    padding: 4px;
    background: #333;
    border: 1px solid #444;
    color: #fff;
    border-radius: 4px;
}

.setting-item input[type="checkbox"] {
    margin-right: 8px;
}

/* 统计组件样式 */
.statistics {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

.period-selector {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
}

.stats-summary {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    margin-bottom: 30px;
}

.stat-item {
    background: #252525;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
}

.stat-value {
    font-size: 32px;
    font-weight: bold;
    color: #007acc;
    margin-bottom: 5px;
}

.stat-label {
    color: #888;
    font-size: 14px;
}

.stats-details {
    background: #252525;
    border-radius: 8px;
    padding: 20px;
}

.stats-details h3 {
    color: #fff;
    margin-bottom: 15px;
}

.stat-entry {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid #333;
}

.stat-date {
    color: #888;
}

.stat-info {
    display: flex;
    gap: 20px;
    color: #ccc;
}

.no-data {
    text-align: center;
    color: #888;
    padding: 20px;
}

/* 主计时器组件样式 */
.main-timer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 20px;
}

.pomodoro-count {
    color: #888;
    margin: 10px 0;
    font-size: 14px;
} `, "",{"version":3,"sources":["webpack://./src/renderer/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,uGAAuG;IACvG,yBAAyB;IACzB,cAAc;IACd,iBAAiB;IACjB,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,SAAS;IACT,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,yBAAyB;IACzB,YAAY;IACZ,eAAe;IACf,iCAAiC;AACrC;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,WAAW;IACX,WAAW;IACX,sBAAsB;IACtB,kBAAkB;IAClB,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,yBAAyB;IACzB,2BAA2B;AAC/B;;AAEA;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,QAAQ;IACR,YAAY;IACZ,yBAAyB;IACzB,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,eAAe;IACf,wBAAwB;AAC5B;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA,aAAa;AACb;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,OAAO;IACP,aAAa;IACb,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,6BAA6B;IAC7B,aAAa;IACb,yBAAyB;IACzB,0BAA0B;AAC9B;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,WAAW;IACX,iBAAiB;IACjB,eAAe;IACf,sBAAsB;AAC1B;;AAEA;;IAEI,WAAW;AACf;;AAEA,WAAW;AACX;IACI,gBAAgB;IAChB,cAAc;IACd,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,cAAc;IACd,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,OAAO;IACP,WAAW;AACf;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,gBAAgB;IAChB,sBAAsB;IACtB,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;AACrB;;AAEA,WAAW;AACX;IACI,gBAAgB;IAChB,cAAc;IACd,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,eAAe;IACf,6BAA6B;AACjC;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,aAAa;IACb,SAAS;IACT,WAAW;AACf;;A